.button{
  background: transparent;
  border: 1px solid #555555;
  color: #555555;
  cursor: pointer;
  font-weight: 600;
  line-height: 1;
  margin-left: 1.3rem;
  outline: none;
  padding: 1.3rem;
  transition: background .4s ease, transform .4s ease;

  &:hover, &:focus{
    background: #E8E8E8;
  }

  &:active{
    background: #CCCCCC;
    transform: scale(1.1);
  }
}

.button--round{
  border-radius: 50%;
  height: 4rem;
  width: 4rem;
}